<template>
  <div class="home">
    <h1 re="message" class="welcome">{{ msg }}</h1>
    <signal v-show="isShow" :isShow="isShow"></signal>
  </div>
</template>

<script>
  //import components
  import Signal from '../components/Signal.vue';
  import Store from '../store.js';

  export default {
    name: 'home',
    components: {
      'signal': Signal,
    },
    props: ['showHome'],
    data () {
      return {
        msg: '',
        isShow: false,
        firstLoad: Store.fetch()
      }
    },
    mounted(){
      this.$nextTick(function () {
        document.body.className = 'home';
        if (!this.firstLoad) {
          this.isShow = true;
          this.msg = "Tap 6 shining dots to explore Joey\'s portfolio !";
        }
      });
    },
    watch: {
      showHome: {
        handler: function () {
          var timer, fullText, currentOffset, onComplete, wordSet;
          var _this = this;
          var indexText = "Tap 6 shining dots to explore Joey\'s portfolio !";

          function Speak(text) {
            fullText = text;
            wordSet = text.split(" ");
            currentOffset = 0;
            onComplete = callback;
            timer = setInterval(onTick, 400);
          }

          function onTick() {
            currentOffset++;
            if (currentOffset == wordSet.length) {
              complete();
              return;
            }
            var text = "";
            for (var i = 0; i < currentOffset; i++) {
              text += wordSet[i] + " ";
            }
            text.trim();
            _this.msg = text;
          }

          function complete() {
            clearInterval(timer);
            timer = null;
            _this.msg = fullText;
            if (onComplete) onComplete();
          }

          function callback() {
            _this.isShow = true;
          }

          if (this.firstLoad) {
            setTimeout(function () {
              Speak(indexText);
            }, 400);
          }
          else {
            _this.isShow = true;
            _this.msg = indexText;
          }
        },
        deep: true
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1.welcome {
    color: rgba(0, 0, 0, 1);
    font-family: 'HYShiGuangTiW';
    font-size: 30px;
    transform: rotateZ(-1deg);
    position: absolute;
    left: 160px;
    top: 20px;
    font-weight: 300;
  }
</style>



