<template>
  <div class="fullscreen-bg" v-if="showIntro">
    <div class="password" v-show="nopass">
      <div class="container">
        <label><b>Please input password</b></label>
        <input type="text" placeholder="Enter Password" required v-model="password" @keyup.enter="checkPassword" autofocus>
        <button type="button" @click="checkPassword">Confirm</button>
      </div>
    </div>
    <video autoplay class="fullscreen-bg-video" ref="video">
      <source src="../assets/intro_video.mp4" type="video/mp4">
    </video>
  </div>
</template>

<script>
  export default {
    name: 'intro',
    data () {
      return {
        video_src: '../assets/intro_video.mp4',
        password: '',
        nopass: false,
        showIntro: true
      }
    },
    methods: {
      checkPassword: function () {
        if (this.password == 'nopassword') {
          this.showIntro = false;
          //触发进入首页事件
          this.$emit('showHome');
        }
        else {
          this.password = '';
        }
      }
    },
    mounted: function () {
      var _this = this;
      this.$refs.video.addEventListener('ended', function () {
        _this.nopass = true;
      });
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .fullscreen-bg {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    z-index: -100;
  }

  .fullscreen-bg-video {
    position: absolute;
    top: 50%;
    left: 50%;
    width: auto;
    height: auto;
    min-width: 100%;
    min-height: 100%;
    transform: translate(-50%, -50%);
  }

  .password {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background: rgba(255, 255, 255, 0.2);
    font-family: 'Roboto';
  }

  .container {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 400px;
    background: #ccc;
    padding: 40px 20px;
    text-align: center;
    border-radius: 5px;
    background: #fff;
  }

  label, input, button {
    display: block;
  }

  label {
    margin-bottom: 30px;
    font-size: 20px;
    color: rgba(0, 0, 0, .87);
  }

  button {
    background-color: #000;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 100%;
    font-size: 14px;
  }

  input {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    box-sizing: border-box;
    font-size: 14px;
  }
</style>



