<template>
  <div id="app">
    <loading v-show="isLoading"></loading>
    <intro v-show="firstLoad" @showHome="viewHome"></intro>
    <router-view v-show="showHome" :showHome="showHome"></router-view>
  </div>
</template>

<script>
  //import components
  import Loading from './components/Loading.vue';
  import Intro from './components/Intro.vue';
  import Store from './store.js';

  export default {
    name: 'app',
    data () {
      return {
        isLoading: true,
        firstLoad: Store.fetch(),
        showHome: false
      }
    },
    components: {
      'loading': Loading,
      'intro': Intro
    },
    methods: {
      viewHome: function () {
        this.firstLoad = false;
        this.showHome = true;
        Store.save(false);
      }
    },
    mounted(){
      var _this = this;
      this.$nextTick(function () {
        setTimeout(function () {
          _this.isLoading = false;
          if (_this.firstLoad == false) {
            _this.showHome = true;
          }
        }, 1000);
      });
    }
  }
</script>

<style>
  /** Roboto **/
  @font-face {
    font-family: 'Roboto';
    src: url("./fonts/Roboto-Regular.ttf");
  }

  @font-face {
    font-family: 'Roboto-Medium';
    src: url("./fonts/Roboto-Medium.ttf");
  }

  /** Georgia **/
  @font-face {
    font-family: 'Georgia';
    src: url("./fonts/Georgia.ttf");
  }

  @font-face {
    font-family: 'Georgia-Italic';
    src: url("./fonts/Georgia-Italic.ttf");
  }

  @font-face {
    font-family: 'Georgia-Bold';
    src: url("./fonts/Georgia-Bold.ttf");
  }

  @font-face {
    font-family: 'HYShiGuangTiW';
    src: url("./fonts/HYShiGuangTiW.ttf");
  }

  html, body {
    margin: 0;
    padding: 0;
  }

  body.home {
    background-image: url(./assets/interior.png);
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    background-color: #999;
  }

  /*body.home::before {*/
  /*content: '';*/
  /*display: block;*/
  /*position: fixed;*/
  /*left: 0;*/
  /*top: 0;*/
  /*!*background: rgba(0,0,0,.5);*!*/
  /*width: 100%;*/
  /*height: 100%;*/
  /*}*/

  #app {
    font-family: 'Georgia', serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: rgba(0, 0, 0, .87);
  }

  .work header {
    height: 64px;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .15);
  }

  .work .content-container {
    margin: 0 auto;
    max-width: 700px;
    padding: 0 20px;
  }

  .work strong {
    font-family: 'Georgia-Bold';
  }

  .work h1 {
    color: rgba(0, 0, 0, .87);
    font-size: 36px;
    line-height: 1.2;
    font-family: 'Roboto';
    margin-top: 35px;
    letter-spacing: -.015em;
    text-align: center;
  }

  .work h3 {
    color: rgba(0, 0, 0, .87);
    font-size: 26px;
    line-height: 1.58;
    font-family: 'Roboto';
    margin-top: 52px;
  }

  .work h4 {
    color: rgba(0, 0, 0, .87);
    font-size: 18px;
    font-family: 'Roboto';
    margin-top: 42px;
  }

  .work h5 {
    color: rgba(0, 0, 0, .87);
    font-size: 16px;
    font-family: 'Roboto';
    margin-top: 22px;
  }

  .work h1 ~ p {
    margin-top: 10px;
  }

  .work p {
    margin-top: 20px;
    font-size: 21px;
    line-height: 1.58;
  }

  .work figure {
    margin: 35px 0 0 0;
  }

  .work figure img, .work figure video {
    max-width: 680px;
    margin-left: auto;
    margin-right: auto;
  }

  .work figure + * {
    margin-top: 38px;
  }

  .work figcaption {
    font-size: 16px;
    font-family: 'Roboto';
    margin-top: 10px;
    color: rgba(0, 0, 0, .68);
    text-align: center;
  }

  .work ol {
    margin-top: 20px;
    padding: 0;
    margin-left: 20px;
  }

  .work ol li {
    font-size: 21px;
    margin-bottom: 14px;
    line-height: 1.58;
  }

  .work a {
    text-decoration: underline;
    color: rgba(0, 0, 0, .87)
  }

  .work blockquote {
    font-size: 30px;
    margin: 55px 0;
    padding: 0 77px;
    line-height: 1.48;
    color: rgba(0, 0, 0, .68);
    font-family: 'Georgia-Italic';
  }

  hr.divider {
    margin-top: 52px;
    margin-bottom: 42px;
    border: 0;
    text-align: center;
    overflow: visible;
    height: 0;
  }

  hr.divider:before {
    content: '...';
    display: inline-block;
    margin-left: .6em;
    color: rgba(0, 0, 0, .68);
    position: relative;
    top: -30px;
    letter-spacing: .6em;
    font-size: 30px;
  }

  .work header {
    padding: 0 20px;
    height: 64px;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.05);
    text-align: center;
  }

  .work footer {
    display: flex;
    padding: 0 20px;
    /*height: 40px;*/
    background-color: #ffffff;
    box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.54);
  }

  .work .previous-work {
    display: inline-block;
    width: 700px;
    margin: 0 auto;
  }

  .work .next-work {
    min-width: 260px;
    position: relative;
  }

  .work .next-work:before {
    content: '';
    position: absolute;
    display: block;
    height: 32px;
    width: 1px;
    background: rgba(0, 0, 0, 0.2);
    top: 6px;
    left: -20px;
  }

  .work .next-work div {
    font-family: 'Roboto';
    font-size: 15px;
    color: rgba(0, 0, 0, 0.68);
  }

  .work .next-work h4 {
    font-family: 'Roboto-Bold';
    font-size: 18px;
    margin: 0;
  }

  .work .logo {
    width: 130px;
    height: 35px;
    display: inline-block;
    margin: 0 auto;
    background-image: url("./assets/logo.svg");
    background-repeat: no-repeat;
    margin-top: 18px;
  }
</style>



