const STORAGE_KEY = 'FIRSTLOAD';
const CLICK_KEY = 'CLICKED';

export  default {
  fetch(){
    return JSON.parse(window.sessionStorage.getItem(STORAGE_KEY) || true);
  },
  save(value){
    window.sessionStorage.setItem(STORAGE_KEY,
      JSON.stringify(value));
  },
  fetchClick(){
    return JSON.parse(window.sessionStorage.getItem(CLICK_KEY) || '[]');
  },
  saveClick(items){
    window.sessionStorage.setItem(CLICK_KEY, JSON.stringify(items));
  },
}



