import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Work from '@/components/Work'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path:'/input-method',
      component: Work
    },
    {
      path:'/sds',
      component: Work
    },
    {
      path:'/navigation',
      component: Work
    },
    {
      path:'/user-testing',
      component: Work
    },
    {
      path:'/competitor-research',
      component: Work
    },
    {
      path:'/know-context',
      component: Work
    },
    {
      path:'/work',
      component: Work
    }
  ]
})



